@import 'colors';
@import 'fonts';

@import 'sections/principles';

html {
	font-size: 62.5%; }
body {
	@include f-regular; }
p, h1 {
	margin: 0; }

.section {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 144rem;
	&__title {
		@include f-bold;
		font-weight: bold;
		font-size: 6rem;
		line-height: 8rem;
		margin-bottom: 6rem; } }

.border {
	border: 1px solid black;
	&-r {
		border: 1px solid red; } }

.container {
	max-width: 144rem;
	align-self: center;
	width: 100%; }


